* {
  box-sizing: border-box;
}
.error-boundary-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.error-boundary-text {
  color: crimson;
  font-size: 2rem;
  letter-spacing: 1px;
  border: 2px solid crimson;
  border-radius: 1.25rem;
  padding: 3rem;
  margin:3rem;
}

@media (max-width: 767px) {
  .error-boundary-text {
    font-size: 1.5rem;
  }
}

@media (max-width: 576px) {
  .error-boundary-text {
    font-size: 1rem;
    margin: 2rem;
  }
}
