}

body {
  color: #000;
  background: #fbf8f5;
  font-size: 16px;
  line-height: 1.4;
  font-family: "sofia_proregular";
}
a {
  color: #000;
}
.black {
  color: #000;
}
.white {
  color: #fff;
}
.grew {
  color: #94999d;
}
.link,
.primary-text {
  color: #cc835c;
}
p {
  margin: 0 0 10px;
}
.row img {
  max-width: 100%;
}

h4 {
