}

.navbar-default {
  background-color: #d7d7d2 !important;
}
.navbar-fixed-top .container {
  width: 100%;
  max-width: 100%;
  position: relative;
}
.navbar-fixed-top.scrolled {
  background-color: #fff !important;
  transition: background-color 200ms linear;
}
.blank_space {
  height: 75px;
  width: 100%;
