  width: 25vw;
  min-width: 130px;
}
.header_logo > a {
  display: inline-block;
}
.header_logo > a > img {
  width: 100%;
  height: auto;
}
.navbar-default .navbar-nav > li > a:hover {
  text-decoration: underline;
}
.navbar-default .navbar-nav > li > a {
  cursor: pointer;
}
.navbar-default .navbar-items{
  flex-direction:row
}

.header_menu_bg {
  background: #d7d7d2;
}
ul.left_menu {
  list-style: none;
  padding: 5px 0 0 15px;
  margin: 0px;
}
ul.left_menu li {
  display: inline-block;
  vertical-align: middle;
}
ul.left_menu li a {
  color: #000;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
}
ul.left_menu li:after {
  content: " / ";
  font-size: 12px;
  padding: 0 1rem;
}
ul.left_menu li:last-child::after {
  content: "";
}
ul.left_menu li a:hover {
  text-decoration: underline;
}

