footer {
  background: #000;
  color: #fff;
  padding: 80px 0px 120px 0px;
}
footer h4.pt2 {
  padding-bottom: 0px;
}
ul.footer_ul_amrc {
  margin: 0px;
  padding: 0px 0px 0px 0px;
