.my-orders-section {
  font-family: "sofia_prosemibold";
}
/* .my-orders-section .Collapsible ,
.my-orders-section .content:hover {
  background: #eeeeee;
} */
.my-orders-section .content {
  background: white;
  /* border-bottom: 1px solid #eee2d7; */
}
.my-orders-section .Collapsible button {
  color: #000;
  background: #fff;
  cursor: pointer;
  font-weight: 400;
  padding: 35px 20px 35px 30px;
  display: inline-block;
  float: none;
  margin: 0px auto;
  text-align: left;
  border: none;
  text-transform: inherit;
  outline: none;
  width: 100%;
  font-size: 16px;
  font-family: "sofia_prosemibold";
}
.my-orders-section .Collapsible .Collapsible__trigger.is-closed button {
  border-bottom: 1px solid #eee2d7;
}
ul.tab_order {
  margin: 0px !important;
  padding: 0px !important;
  list-style: none;
  display: grid;
  grid-template-columns: 3fr 2fr 2fr 1fr;
}
ul.tab_order li {
  padding: 4px;
}

ul.tab_order li.orders4 {
  /* place-self grid child property to center element*/
  place-self: center;
  font-size: 22px;
}
.collapsible.active .fa-angle-right {
  display: none;
}
.collapsible.active .fa-angle-up {
  display: inline-block;
}
.collapsible .fa-angle-up {
  display: none;
}
ul.tab_order .small_title {
  display: block;
}
.my-orders-section .small_title {
  font-size: 14px;
  color: #a19aa0;
}

ul.open_tab_order {
  margin: 0px !important;
  padding: 20px 0px 30px 0px !important;
  display: inline-block;
  width: 100%;
  list-style: none;
}
ul.open_tab_order li {
  display: inline-block;
  float: left;
}
ul.open_tab_order li.add1 {
  width: 50%;
}
ul.open_tab_order li.add2 {
  width: 49%;
}
ul.open_tab_order .small_title {
  display: block;
}
.my-orders-section .small_title {
  font-size: 14px;
  color: #a19aa0;
}

.collapsible {
  color: #fff;
  background: #000000;
  cursor: pointer;
  font-weight: 600;
  padding: 15px 10px 15px 10px;
  display: inline-block;
  float: none;
  margin: 0px auto;
  text-align: left;
  border: none;
  text-transform: uppercase;
  outline: none;
  width: 150px;
  font-size: 16px;
}
/* .collapsible.active span.read-btn {
  display: none;
}
.collapsible span.close-btn {
  display: none;
} */
.active span.close-btn {
  display: inline-block;
}
.content {
  padding: 0 18px;
  /* display: none; */
  overflow: hidden;
  font-size: 16px;
}

.scrollup {
  font-size: 20px;
  color: #fff;
  background-color: #000;
  border: 0px;
  border-radius: 3px;
  text-align: center;
  line-height: 30px;
  padding: 6px;
  position: relative;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 30px;
  right: 20px;
  cursor: pointer;
}
.scrollup i {
  position: absolute;
  top: 10px;
  left: 0px;
  right: 0px;
}

.free_shipping_promo {
  background: #fff;
  text-align: center;
  font-size: 18px;
  color: #000;
  display: none;
  padding: 10px 0px;
}
.cart_desktop {
  display: none;
  position: absolute;
  right: 20px;
  top: 25px;
  z-index: 99999;
}
.cart_desktop span {
  color: #000;
  letter-spacing: 2px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
}
.sidebar {
  height: 100%;
  width: 45%;
  position: fixed;
  z-index: 1;
  top: 0;
  background-color: #fbf8f5;
  overflow-x: hidden;
  transition: 0.4s;
  z-index: 9999;
}

.sideleft {
  height: 100%;
  width: 45%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #fbf8f5;
  overflow-x: hidden;
  transition: 0.4s;
  z-index: 9999;
}
.sideleft a {
  text-decoration: none;
  transition: 0.3s;
}
.sideleft a:hover {
  text-decoration: none;
}
.sideleft .closebtn {
  position: absolute;
  top: 20px;
  right: 10px;
  font-size: 13px;
  z-index: 99999;
}

.sideright {
  height: 100%;
  width: 45%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #fbf8f5;
  overflow: hidden;
  transition: 0.4s;
  z-index: 9999;
}
.sideright a {
  text-decoration: none;
  transition: 0.3s;
}
.sideright a:hover {
  text-decoration: none;
}
.sideright .closebtn {
  position: absolute;
  top: 20px;
  left: 10px;
  font-size: 13px;
  z-index: 99999;
}

.sideright img {
  max-width: 100%;
}
.sideleft img {
  max-width: 100%;
}

.cart_section {
  position: relative;
  height: 100%;
  width: 100%;
  display: inline-block;
  overflow-y: auto;
}

.cart_items {
  width: 70%;
  margin: 0px auto;
  padding: 20px 10px 50px 10px;
}

.cart_items .empty_cart {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cart_items .empty_cart + button.login_btn {
  background-color: black;
}
.cart_items .empty_cart + button.login_btn:hover {
  background-color: #666666;
}
.cart_items .header-title {
  font-size: 20px;
  text-align: center;
  padding: 20px 0px 20px 0px;
}
/* .cart_section.no-items {
  height: auto !important;
} */

.cart-details_product {
  position: relative;
}
.cart-details_product-image {
  width: 40%;
  float: left;
  background: #eee2d7;
}
.cart-details_product-details {
  background: #fff;
  padding: 10px 15px 5px 15px;
  float: left;
  width: 60%;
  position: relative;
  height: 170px;
  font-weight: 600;
}
.cart-details_product-details h4.description {
  color: #999999;
}
.cart-details_product-image img {
  max-width: 100%;
}

.renewal-icon {
  font-size: 16px;
  width: 40px;
  height: 40px;
  border-radius: 40px;
  background: #bcbcbc;
  color: #fff;
  text-align: center;
  line-height: 42px;
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.icon_close {
  position: absolute;
  right: 30px;
  top: 20px;
  color: #bcbcbc;
}
button.closebtn {
  border: 2px solid #d6d6d2;
  width: 40px;
  height: 40px;
  line-height: 28px;
  border-radius: 40px;
  text-align: center;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
button.closebtn:hover {
  border: 2px solid #000;
}
button.closebtn .glyphicon {
  top: 0px !important;
  color: #000 !important;
}
button.closebtn:focus {
  outline: none;
}

.cart_numbers ul {
  margin: 0px !important;
  padding: 30px 0px 10px 0px;
  list-style: none;
}
.cart_numbers li:first-child {
  border-top: 2px solid #eee2d7;
}
.cart_numbers li {
  font-size: 18px;
  color: #666666;
  padding: 12px 10px 12px 10px;
