  position: relative;
  left: 40%;
  bottom: 10%;
  cursor: pointer;
}

/* camera button container camera modal */
.camera-btn_container {
  display: flex;
  width: 32rem;
  display: -webkit-flex;
  justify-content: space-around;
  align-items: center;
}

.camera-btn_container .capture_btn,
.camera-btn_container .capture-close_btn {
  all: unset;
  background: black;
  color: white;
  background: black;
  padding: 1rem 3rem;
  cursor: pointer;
  transition: 0.2s;
}

/* design for stripe element */
.StripeElement {
  background-color: white;
  height: 40px;
  padding: 10px 12px;
  border-radius: 4px;
  border: 1px solid transparent;
  box-shadow: 0 1px 3px 0 #e6ebf1;
  -webkit-transition: box-shadow 150ms ease;
  transition: box-shadow 150ms ease;
  margin-bottom: 15px;
}

.StripeElement--focus {
  box-shadow: 0 1px 3px 0 #cfd7df;
}

.StripeElement--invalid {
  border-color: #fa755a;
}

.StripeElement--webkit-autofill {
  background-color: #fefde5 !important;
}

